import { supabase } from '../config/supabase';
import type { User } from '../config/supabase';

export const userService = {
  async saveNewUser(userData: Omit<User, 'id' | 'created_at'>): Promise<User | null> {
    try {
      const { data, error } = await supabase
        .from('users')
        .insert([
          {
            ...userData,
            first_launch: new Date().toISOString(),
          },
        ])
        .select()
        .single();

      if (error) {
        console.error('Error saving user:', error);
        return null;
      }

      return data;
    } catch (error) {
      console.error('Error in saveNewUser:', error);
      return null;
    }
  },

  async getUserByTelegramUsername(telegram_username: string): Promise<User | null> {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')
        .eq('telegram_username', telegram_username)
        .single();

      if (error) {
        console.error('Error fetching user:', error);
        return null;
      }

      return data;
    } catch (error) {
      console.error('Error in getUserByTelegramUsername:', error);
      return null;
    }
  },
}; 